@font-face {
  font-family: 'Cubano Normal';
  src: local('Cubano Normal'), local('Cubano Normal'),
  url('./fonts/Cubano.ttf') format('truetype');
  font-weight: 400;
  font-style: normal;
}
* {
  overflow-wrap: break-word;
  box-sizing: border-box;
  font-family: "Cubano Normal", sans-serif;
  padding: 0;
  margin: 0;
}
html{
  --color-white:#ffffff;
  --color-black:#12181B;
  --color-grey:#2A2E35;
  --color-white-blue:#B2BECD;
}
body{
  padding: 0;
  background-color: var(--color-black);
}
h1,h2,h3,h4,h5,input,p,textarea{
  color: var(--color-white);
}
